import React from 'react'
import TOP_BANNER from '../resources/Home/banner-top.svg'
import SEARCH_ICON from '../resources/Home/search-icon.svg'
import '../styles/home.css'

const Home = () => {
  return (
    <>
      {/* 1 Home - 1 */}
      <section className='section-home-1'>
        <div className='row m-0 top-container'>
          <div className='col-12 p-0'>
            <img src={TOP_BANNER} class='img-fluid' alt='banner' />
          </div>
          <div class='input-group search_container m-4 rounded border'>
            <span class='input-group-text border-0'>
              <img src={SEARCH_ICON} class='img-fluid' alt='search' />
            </span>
            <input
              type='text'
              class='form-control border-0'
              placeholder='Seach Product'
            />
          </div>
        </div>

        <div className='row m-0 product-container'>
          {/* <div className='col-12 p-0'>
            <img src={TOP_BANNER} class='img-fluid' alt='banner' />
          </div>
          <div class='input-group search_container m-4 rounded border'>
            <span class='input-group-text border-0'>
              <img src={SEARCH_ICON} class='img-fluid' alt='search' />
            </span>
            <input
              type='text'
              class='form-control border-0'
              placeholder='Seach Product'
            />
          </div> */}
        </div>
      </section>
    </>
  )
}

export default Home
